<template>
    <div class="classif-wrap">
        <div v-title data-title="我有好货">我有好货</div>
        <header  class="header">
            <span @click="btnGo" class="btnGo"></span>
            我有好货</header>
         <div class="feedback-wrap">
            <div class="from-wrap">
                <div class="input-main clearfix">
                    <span class="fl name">商品名称：</span>
                    <input type="text" class="fl" placeholder="请输入商品名称..." v-model="goods_title">
                </div>
                <div class="input-main clearfix">
                    <span class="fl name">供货商名：</span>
                    <input type="text" class="fl theme" placeholder="请输入供货商..." v-model="supplier">
                </div>
                <div class="input-main clearfix">
                    <span class="fl name">供应商地址：</span>
                    <input type="text" class="fl theme" placeholder="请输入公司地址..." v-model="buss_address">
                </div>
                <div class="input-main clearfix">
                    <span class="fl name">供货时间：</span>
                    <input type="text" class="fl theme" placeholder="请输入供货时间..." v-model="delivery_time">
                </div>
                <div class="input-main clearfix">
                    <span class="fl name">发货能力：</span>
                    <input type="text" class="fl theme" placeholder="请输入发货能力..." v-model="delivery_capacity">
                </div>
                
                <div class="input-main clearfix">
                    <span class="fl name">联系人姓名：</span>
                    <input type="text" class="fl theme" placeholder="请输入联系人姓名..." v-model="link_man">
                </div>
                <div class="input-main clearfix">
                    <span class="fl name">联系电话：</span>
                    <input type="tel" class="fl" placeholder="请输入联系电话..." maxlength="11" v-model="mobile">
                </div>
                <div class="input-main clearfix">
                    <span class="fl name">邮箱：</span>
                    <input type="text" class="fl" placeholder="请输入联系邮箱..."  v-model="email">
                </div>
                <div class="input-main clearfix">
                    <span class="fl name">商品描述：</span>
                </div>
                <textarea placeholder="请输入商品详细描述..." v-model="goods_dec"></textarea>
                <div class="img-wrap clearfix">
                    <div class="list-img fl" v-for="(imgas,index) in imgs" :key="imgas.id" v-show="imgs.length >= 1" @click="imgShow(index)">
                        <div class="del el-icon-circle-cross" @click.stop="del(index)"></div>
                        <img :src="imgs[index].img">
                    </div>
                    <div class="input-btn fl" v-show="imgs.length < 3">
                        <input type="file" @change="add_img($event)" ref="int">
                    </div>
                </div>
                <div class="max-img" v-show="maximg" @click="hideImg">
                    <img :src="maximg">
                </div>
            </div>
            <div class="btn-wrap">
                <button @click="submit">提交</button>
            </div>
        </div>
        <div class="load" v-show="load" @touchmove.prevent><mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner></div>
        <!-- <div class="load-wrap" v-show="$store.state.class_load" @touchmove.prevent><mt-spinner type="triple-bounce" color="rgb(38, 162, 255)"></mt-spinner></div> -->
    </div>
</template>
<script>
    import { Toast } from 'mint-ui';
    import QS from 'qs';
    export default {
        name : 'classification',
        data(){
            return {
                status:[true,false],
                load:false,
                data:'',
                show:false,
                con:'',
                goods_title:'',
                supplier:'',
                mobile:'',
                goods_dec:'',
                email:'',
                delivery_time:'',
                delivery_capacity:'',
                buss_address:'',
                link_man:'',
                imgs:[],
                maximg:null,
            }
        },
        methods:{
            add_img(ev){
                const reader = new FileReader(),
                      img1 = ev.target.files[0],
                      that = this;
                reader.readAsDataURL(img1);
                reader.onload = function(){
                    that.imgs.push({img:this.result,type:'data:'+img1.type+';base64,'})
                }
                this.$refs.int.value = '';
            },
            del(index){
                this.imgs.splice(index,1)
            },
            imgShow(index){
                this.maximg = this.imgs[index].img;
            },
            hideImg(){
                this.maximg = null;
            },
            btnGo(){
                this.$router.go(-1);
            },
            //提交
            submit(){
                let reg = /^(1\d{10})$|^(\d{4}-\d{7,8})$/;
                if(this.goods_title == ''){
                    Toast('商品名称不能为空！')
                }else if(this.supplier == ''){
                    Toast('供货商不能为空！')
                }else if(this.delivery_time == ''){
                    Toast('供货时间不能为空！')
                }else if(this.delivery_capacity == ''){
                    Toast('发货能力不能为空！')
                }else if(this.buss_address == ''){
                    Toast('公司地址不能为空！')
                }else if(this.link_man == ''){
                    Toast('联系人姓名不能为空！')
                }else if(!(/^1[345789]\d{9}$/.test(this.mobile))){
                    Toast('手机号码格式错误！')
                }else if(!(/^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(this.email))){
                    Toast('邮箱不能为空！')
                }else if(this.goods_dec == ''){
                    Toast('商品描述不能为空！')
                }else{
                    this.axios.post(API_URL + 'Home/IHaveGood/add',QS.stringify({
                    goods_title:this.goods_title,
                    supplier:this.supplier,
                    delivery_time:this.delivery_time,
                    delivery_capacity:this.delivery_capacity,
                    buss_address:this.buss_address,
                    linkman:this.link_man,
                    mobile:this.mobile,
                    goods_dec:this.goods_dec,
                    app_user_id:sessionStorage.getItem('user_ID'),
                    email : this.email,
                    img:this.imgs
                })).then((res) => {
                    if (res.data.status == 0) {
                        Toast({
                            message: res.data.msg,
                        });
                    }else{
                        Toast({
                            message: res.data.msg,
                        });
                        this.$router.push({
                                path:'/person'
                            });
                    }
                }).catch((err) => {
                    console.log(err);
                });
            } 
        }
            
        },
        mounted() {
            document.body.scrollTop = 0;
            if(!sessionStorage.getItem('user_ID')){
                        this.$router.push({
                        path:'/LogoIn'
                    });
                    return;
                }
           
        },
        updated(){
            // var that = this;
            // setTimeout(function(){
            //     that.$store.state.class_load = false;
            // },500);
        },
        destroyed(){
            this.scrollWatch = false;
        },
    }
</script>
<style lang="less" scoped>
    .classif-wrap{
        background:#f2f2f2;
        .header {
            background: #ed3851;
            text-align: center;
            color: #fff;
            font-size: .36rem;
            font-weight: 700;
            line-height: .62rem;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 1;
            width: 100%;
            height: .62rem;
            padding: .16rem 0;
            .btnGo{
                position:absolute;
                left:.2rem;
                top:50%;
                margin-top:-.23rem;
                width:.46rem;
                height:.46rem;
                background:url(../../assets/btn-return.png) no-repeat;
                background-size:100% 100%;
            }
        }
        .feedback-wrap {

            .from-wrap{
                padding-top: .94rem;
                .up-loading{
                    background:url(../../assets/addimg00.png)no-repeat;
                    background-size:100% 100%; 
                    // border:.01rem dashed #bdbdbd;
                    width: 1.5rem;
                    height: 1.5rem;
                    line-height: 3rem;
                    text-align: center;
                    margin:.2rem;
                    input{
                        width: 100%;
                        height: 100%;
                        opacity: 0;
                    }
                }
                textarea{
                    padding:0 .2rem;
                    border:none;
                    height: 3.6rem;
                    width: 100%;
                    resize:none;
                    outline: none;
                    padding-bottom:.2rem;
                    font-size:.32rem;
                    color:#333;
                }
                .input-main{
                    padding:0 .2rem;
                    height: 1rem;
                    background:#fff;
                    line-height: 1rem;
                    margin-top: .05rem;
                    .name{
                        font-size:.32rem;
                        color:#969696;
                    }
                    .theme {
                        text-indent:16px;
                    }
                    input{
                        width: 5rem;
                        height: 100%;
                        outline:none;
                        font-size:.32rem;
                        border:none;
                    }
                }
                .input-main:first-child{
                    margin-top:0;
                }
                .img-wrap{
                    margin: 0 .2rem;
                    .list-img{
                        margin-right:.3rem;
                        margin-top:.2rem;
                        position:relative;
                        img{
                            width:1.5rem;
                            height:1.5rem;
                        }
                        .del{
                            position:absolute;
                            top:-.15rem;
                            right:-.15rem;
                            font-size:.3rem;
                        }
                    }
                    .list-img:nth-child(4n){
                        margin-right:0;
                    }
                    .input-btn{
                        position:relative;
                        overflow:hidden;
                        width: 1.5rem;
                        height: 1.5rem;
                        background:url(../../assets/img-btn.jpg) no-repeat;
                        background-size:100% 100%;
                        margin-top:.2rem;
                        input{
                            width: 100%;
                            height: 100%;
                            position:absolute;
                            left: 50%;
                            top: 50%;
                            margin-left: -50%;
                            margin-top: -50%;
                            outline: none;
                            opacity: 0;
                        }
                    }
                }
                .max-img{
                    width: 100%;
                    height: 100%;
                    position:fixed;
                    top:0;
                    left:0;
                    background:rgba(0,0,0,.7);
                    z-index:999;
                    img{
                        width: 85%;
                        height: 40%;
                        position:absolute;
                        left:0;
                        top:0;
                        right:0;
                        bottom:0;
                        margin:auto;
                    }
                }
            }
            .btn-wrap{
                padding:.6rem .2rem;
                button{
                    width: 100%;
                    height: .9rem;
                    background:#ed3851;
                    border:none;
                    color:#fff;
                    font-size:.32rem;
                    outline: none;
                }
                button:active{
                    box-shadow: 0 5px 5px #ccc;
                }
            }
        }
        
    }
</style>


